/* nameprompt.css */
.name-prompt-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2;
    backdrop-filter: blur(3px);
  }
  
  .name-prompt-container {
    border-radius: 2em;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
    width: 100%;
    margin: 0 var(--space-3);
    max-width: var(--space-7);
    display: flex;
    gap: var(--space2);
    flex-direction: column;
  }
  
  .name-prompt-container h2 {
    margin-bottom: var(--space-2);
  }
  
  .name-prompt-container input {
    width: 100%;
    margin-bottom: var(--space-2);
  }
  
  .name-prompt-container button {
    padding: var(--space-2) var(--space-2);
    color: white;
    border: none;
    cursor: pointer;
    width: 100%;
  }
  